
.welcome header .register-button{background-color:transparent;color:#fff;font-size:.9em;padding-right:0
}
.welcome header .login-button{padding:0;margin:0;background-color:transparent;color:#fff;font-size:.9em
}
.welcome header .action-input{padding:1em .75em
}
.main-center{float:left;padding-left:1.4em;padding-top:1em;width:860px;overflow:hidden;text-align:center
}
.video-box{display:flex;flex-direction:row;flex-wrap:wrap;margin-right:-20px;text-align:left
}
.video__item{margin-right:20px;margin-bottom:20px;padding:2px;flex:0 0 31%
}
.video__item>.hd{position:relative
}
.video__item>.hd>img{display:block;width:100%;height:150px
}
.video__item>.hd>i{position:absolute;right:5px;bottom:5px;color:#fff;font-style:normal;background-color:rgba(0,0,0,0.8);padding:2px 4px;font-size:12px;border-radius:2px
}
.video__item>.bd h3{margin:0;padding-top:12px;font-size:14px;line-height:22px;color:#64696f
}
.video__item>.bd .avatar{line-height:16px;font-size:12px;color:#a4a9ac
}
.video__item>.bd .avatar>img{margin-right:5px;width:16px;height:16px;border-radius:50%;vertical-align:top
}
.btns{display:inline-block;padding:10px;margin:20px
}
.btns>i{display:inline-block;padding:0 10px;line-height:28px;color:#b1b2b2;font-style:normal;font-size:14px;cursor:pointer
}
.btns>i.link{margin:0;padding:0 10px;vertical-align:middle;height:2px;background:#181212
}
.btns>i.disabled{border:1px solid #222;color:#222
}
.btns>i.current{border:1px solid #992c2c
}
.btns>i:last-child{position:relative;margin-left:0
}
.btns i+i{margin-left:5px
}
.tag-box{padding-top:10px;margin:0 auto;width:100%
}
.tag__item{position:relative;margin-bottom:10px;float:left;padding:5px 10px;width:25%;color:#cdcdcd;border:1px solid #992c2c
}
.tag__item>img{position:absolute;width:100%;height:100%;opacity:0
}
.banner__item{margin-bottom:15px
}
.banner__item img{display:block;margin:0 auto;width:100%;height:auto
}
@media (max-width: 810px){
.main-center{padding-left:0;width:100%
}
.video-box{margin-right:0;display:flex;flex-wrap:wrap;padding:0 10px
}
.video__item{flex:0 0 50%;margin-right:0;padding:0 10px
}
.video__item .hd img{height:250px
}
}
@media (max-width: 600px){
.video-box{margin-right:0;display:block
}
.video__item{margin:0 10px 20px
}
.video__item .hd img{height:250px
}
.tag-box{margin:0 5%;width:90%
}
.btns{margin:10px 5%
}
.btns>i:last-child{padding-left:10%
}
.btns>i:last-child:after{width:30%
}
.banner__item{padding:0 10px
}
.banner__item img{width:100%
}
}



